import {meanAndSd, toPrec} from './utils.js'
// import {invt} from './invt.js'


function setCellExpectationAndVariances (cell, prfx) {
  /**
  mutate cell to set expectation and variance of quantities
  */
  cell.data.forEach(item => {
    // cell data can have a cached version of a var deleted from expression.
    // This is a feature because it's useful to avoid user from loosing data
    // defined on parameter field. Here we check and skeep a var on cache
    
    //filter interpolation have `object` type
    if (typeof item.dist === 'object') {
      item.y = (item.y ? item.y : undefined)
      item.u = Math.sqrt(Math.pow(item.args[1]*2, 2) / 12)
      return
    }

    if (item.dist === 'Input') {
      let floatArray = item.args.map(parseFloat).filter(x => !isNaN(x))

      let [mean, sd] = meanAndSd(floatArray.map(x => parseFloat(x)*prfx[item.pref]))

      let u = sd / Math.sqrt(floatArray.length)
      
      // TODO: detect the prec to avoid cuting disnificante digits at some situations
      item.y = toPrec(mean, 15)
      item.u = toPrec(u, 4)
      return
    }
    if (item.dist === 'Resol') {
      item.y = 0
      item.u = Math.sqrt(Math.pow(item.args[1]*2, 2) / 12)
      return
    }
    if (item.dist === 'Uniform') {
      let a = item.args[0]
      let b = item.args[1]

      item.y = (item.args[0] + item.args[1]) / 2
      item.u = Math.sqrt(Math.pow(b - a, 2) / 12)
      return
    }
    if (item.dist === 'Arcsine') {
      let a = item.args[0]
      let b = item.args[1]

      item.y = (item.args[0] + item.args[1]) / 2
      item.u = Math.sqrt(Math.pow(b - a, 2) / 8)
      return
    }
    if (item.dist === 'Triangular') {
      let a = item.args[0]
      let b = item.args[1]

      item.y = (item.args[0] + item.args[1]) / 2
      //item.u = ((a * a + b * b + c * c - a * b - a * c - b * c) / 18)
      item.u = ((b - a)/2)/Math.sqrt(3)
      return
    }
    if (item.dist === 'CTrap') {
      let a = item.args[0]
      let b = item.args[1]

      item.y = (item.args[0] + item.args[1]) / 2
      item.u = Math.sqrt(Math.pow(b - a, 2) / 12 + Math.pow(item.args[2], 2) / 9)
      return
    }
    if (item.dist === 'Trap') {
      let a = item.args[0]
      let b = item.args[1]
      
      item.y = (item.args[0] + item.args[1]) / 2
      item.u = Math.sqrt(Math.pow(b - a, 2) / 24 * (1 + Math.pow(item.args[2], 2)))
      return
    }
    if (item.dist === 'StudentsT') {
      item.y = item.args[0]
      let dof = item.args[2]
      // let k = Math.abs(invt((1 - (cell.p || 0.95))/2, dof))
      // item.u = item.args[1] * k
      item.u = item.args[1] * Math.sqrt(((dof  > 2) ? dof / (dof - 2) : (dof > 1) ? Infinity : undefined))
      return
    }
    if (item.dist === 'Normal') {
      item.y = item.args[0]
      let std = item.args[1]
      item.u = std
      return
    }
  })
}


export {setCellExpectationAndVariances}