<template><span>
                            <!-- a|mean|loc -->
                            <span class="field ml-1" style="font-family: sans-serif;">
                              <span class="control">
                                <a class="button is-small is-static has-background-dark has-text-primary-dark arg-name">a</a>
                              </span>
                              <span class="control">
                                
                                <!-- a -->

                                <number-input 
                                  v-bind:class="a && (parseFloat(a) > parseFloat(b) ? 'has-background-danger' : '')"
                                  style="width: 80px;"
                                  class="input is-small"
                                  @change="$emit('send', quantity)"
                                  v-model="a"
                                  name="a"
                                  autocomplete="off">
                                </number-input>
                              </span>
                            </span>

                            <!-- b|std_dev|scale -->
                            <span class="field ml-1" style="font-family: sans-serif;">
                              <span class="control">
                                <a class="button is-small is-static has-background-dark has-text-primary-dark arg-name">b</a>
                              </span>
                              <span class="control">
                                
                                <!-- b -->

                                <number-input
                                  v-bind:class="
                                    b && (parseFloat(a) > parseFloat(b) ? 'has-background-danger' : '')"
                                  style="width: 80px;"
                                  class="input is-small"
                                  @change="$emit('send', quantity)"
                                  v-model="b"
                                  name="b"
                                  autocomplete="off">
                                </number-input>
                              </span>
                            </span>

</span></template>

<script>
import NumberInput from './NumberInput.vue'

export default {
  name: 'uniform-dist',
  props: ['quantity'],
  emits: ['send'],
  components: {
    NumberInput
  },
  watch: {
    'a': {
      handler () {
        let obj = JSON.parse(JSON.stringify(this.quantity))
        obj.a = this.a
        this.$emit('send', obj)
      }
    },
    'b': {
      handler () {
        let obj = JSON.parse(JSON.stringify(this.quantity))
        obj.b = this.b
        this.$emit('send', obj)
      }
    }    
  },
  data () {
    return {
      'a': undefined,
      'b': undefined,
    }
  },
  methods: {
  },
  created () {
    this.a = this.quantity.args[0]
    this.b = this.quantity.args[1]
  },
  mounted () {
  },
  computed: {
  }
}
</script>

